$fuente-parrafos: 'Inter', sans-serif;
$fuente-titulos: 'Staatliches';
$color-primario: rgba(27, 37, 128, 1);
$color-secundario: rgba(204, 166, 31, 1);
$color-terciario: rgba(105, 120, 255, 1);


.logoPag {
  width: 60px;

}

* {
  box-sizing: border-box;
}

body {
  width: 100%;
  margin: 0px;
}

.nombrePag {
  color: white;
}


.mainPagInicio {
  width: 100%;
  /*background-image: url("../img/coinbase-1-1.png");*/
  .imagenPagInicio {
    width: auto;
  }
  .botonActivosPagInicio {
    z-index: 1;
    position: relative;
    color:gold;
  }
}

.cabecera {

  background-color: $color-primario;
  width: 100%;
  position:fixed;

  .listaCabecera {
    display:flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items:center;
    gap: 25px;
    width: 100%;
    /*border-bottom: 2px solid rgb(196, 182, 182);
    padding: 10px;*/

  }
}

.menuNavegacionNav {

 /*ul {
    
    gap: 25px;
    width: 100%;
    border-bottom: 2px solid rgb(196, 182, 182);
    padding: 10px;
    padding-bottom:20px;
 }*/


  li {
    /*text-decoration: underline #CC0000 3px;*/
    list-style-type: none;
    font-family: $fuente-titulos;
  }


  a {
    text-decoration: none;
    color: white;
  }


  .listaCabecera {
    font-size:14px;
  }

}



#menuNavPag2 {

    .SegundoEnlace {
        text-decoration: none;
        text-decoration: underline #CC0000 3px;

    }
}


#menuNavPag3 {
    .TercerEnlace {
    text-decoration: none;
    text-decoration: underline #CC0000 3px;

    }
}



.piePagina {
    background-color: $color-primario;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    padding:10px;

    p {
        color:  #FFFFFF;
        font-size: 12px;
        margin-left: 120px;
        font-family: Arial, Helvetica, sans-serif;    
    }
}


.imagenPie {
    margin-left: 120px;
}

